	module.exports = {"data":{"markdownRemark":{"html":"<p><em>It’s time to level up and get ready for your A race!</em></p>\n<p>araceathlete.com is a place to share and track events, keep an eye on your Watts per Kilograms and see how your week is progressing at a glance.  </p>\n<p>What is an <strong>A race</strong> you ask? While you may have other important events, an <strong>A race</strong> is <em>THE</em> event you plan and train for all year. It is the pinnacle of your season. It is the point of all that suffering.</p>\n<p>But we digress, by connecting with your Strava account, you can start tracking your fitness in preparation for your A Race and get insights into how your week is stacking up. Put away those spreadsheets and let us do the tracking.</p>","frontmatter":{"title":"Welcome to A Race athlete","date":"2017-06-01T00:19:00.000Z","path":"/welcome-to-araceathlete/","parent":"/home/hypersprite/Documents/code/araceathlete-gatsby-material-ui-v1/src/pages/blog-posts/welcome-to-araceathlete.md absPath of file","tags":"newbie","description":null,"cover":null}}},"pathContext":{"slug":"/blog-posts/welcome-to-araceathlete/"}}

